.detail-forecast div p {
    font-size: 18px;
}
.flex-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.glyph {
    font-family: 'Glyphter';
    font-size: 25px;
}
.pressure {
    color: #2D9ECF;
}
.humidity {
    color: #046DCF;
}
.low-temp {
    color: #1056CF;
}
.high-temp {
    color: #CF4141;
}
.wind {
    color: rgb(151, 151, 151);
}
.cloudiness {
    color: rgb(104, 101, 101);
}
.city {
    font-size: 50px;
    margin-bottom: -10px;
}
.temperature {
    font-size: 55px;
}
.weather-description {
    margin-bottom: 0;
}
.bold {
    font-weight: bold;
    font-size: 20px;
}
.detail-forecast {
    animation-name: zoom;
    animation-duration: 1s;
    -webkit-animation-name: zoom;
    -webkit-animation-duration: 1s;
}
.show-weekly-forecast {
    font-size: 25px;
}
@keyframes zoom {
    from{-webkit-transform: scale(0);transform: scale(0)}
    to{-webkit-transform: scale(1);transform: scale(1)}
}
@-webkit-keyframes zoom {
    from{-webkit-transform: scale(0)}
    to{-webkit-transform: scale(1)}
}
.city-list {
    display: -webkit-flex;
    display: flex;
}
.city-list li {
    list-style: none;
    text-transform: capitalize;
    font-size: 30px;
    color: rgb(155, 44, 44);
    -webkit-animation-name: show;
            animation-name: show;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}
.city-list li:not(:last-child) {
    margin-right: 30px;
}
.city-list li:hover {
    cursor: pointer;
    text-decoration: underline;
}
@-webkit-keyframes show {
    from{opacity: 0}
    to{opacity: 1}
}
@keyframes show {
    from{opacity: 0}
    to{opacity: 1}
}
.table-glyph {
    font-family: 'Glyphter';
}
.glyph3 {
    font-family: "Glyphter3";
    color: white;
}
small, .table-glyph, .glyph3 {
    margin-left: 5px;
}
.tab-row:hover {
    background-color: #2D9ECF;
    color: white;
    cursor: pointer;
    border-left: 1px solid white;
    border-right: 1px solid white;
}
.weekly-forecast {
    position: relative;
}
.weekly-forecast p {
    font-size: 30px;
    font-weight: bold;
    margin-top: 20px;
}
.glyph2 {
    font-family: 'Glyphter2';
    font-size: 30px;
    color: #4A66C6;
}
.glyph2:hover{
    color: #007BFC;
}
.back:hover, .weekly-forecast a:hover {
    text-decoration: none;
}
.back>.glyph2 {
    font-size: 75px;
    position: relative;
    left: -485px;
    top: 50px;
}
.weekly-forecast a {
    position: absolute;
    left: 0;
    top: 10px;
    line-height: 0px;
}
.back-label {
    line-height: 30px;
    color: #4A66C6
}

body {
    background: rgba(212,228,239,1);
    background: -webkit-gradient(left top, right top, color-stop(0%, rgba(212,228,239,1)), color-stop(100%, rgba(134,174,204,1)));
    background: linear-gradient(to right, rgba(212,228,239,1) 0%, rgba(134,174,204,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d4e4ef', endColorstr='#86aecc', GradientType=1 );
} 
.App {
    width: 80%;
    margin: 0 auto;
    text-align: center;
}
@font-face {
    font-family: 'Glyphter';
    src: url(/weather-forecast/static/media/Glyphter.a372b5f2.ttf) format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Glyphter2';
    src: url(/weather-forecast/static/media/Glyphter2.11757b78.ttf) format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Glyphter3';
    src: url(/weather-forecast/static/media/Glyphter3.e988ba8a.ttf) format('truetype');
    font-weight: normal;
    font-style: normal;
}

